import dayjs from "dayjs";
import Image from "next/image";
import {getRandomInterviewCover} from "@/lib/utils";
import Link from "next/link";
import {Button} from "@/components/ui/button";

const InterviewCard = ({ interviewId, type, userId, role, techStack, createdAt }: InterviewCardProps) => {
    const feedback = null as Feedback || null;
    const normalizedType = /mix/gi.test(type) ? "Mixed" : type;
    const formattedDate = dayjs(feedback?.createdAt || createdAt || Date.now()).format("MMM D YYYY");

    return (
        <div className="card-border w-[360px] max-sm:w-full min-h-96 relative">
            <div className="card-interview p-6 flex flex-col gap-4">
                {/* Badge */}
                <div className="absolute top-0 right-0 w-fit px-4 py-2 rounded-bl-lg bg-light-600">
                    <p className="badge-text">{normalizedType}</p>
                </div>

                {/* Cover */}
                <Image
                    src={getRandomInterviewCover()}
                    alt="cover"
                    width={90}
                    height={90}
                    className="rounded-full object-cover size-[90px]"
                />

                {/* Role */}
                <h3 className="mt-5 capitalize">{role} Interview</h3>

                {/* Date + Score + Feedback */}
                <div className="flex flex-col gap-3 mt-3">
                    <div className="flex flex-row gap-6 items-center">
                        <div className="flex flex-row gap-2 items-center">
                            <Image src="/calendar.svg" alt="calendar" width={22} height={22} />
                            <p>{formattedDate}</p>
                        </div>
                        <div className="flex flex-row gap-2 items-center">
                            <Image src="/star.svg" alt="star" width={22} height={22} />
                            <p>{feedback?.totalScore || "---"}/100</p>
                        </div>
                    </div>

                    <p className="line-clamp-2 mt-5">
                        {feedback?.finalAssessment ||
                            "You haven't taken this interview yet. Take it now to improve your skills."}
                    </p>
                </div>

                {/* Footer */}
                <div className="flex flex-row justify-between">
                    <DisplayTechIcons techStack={techstack} />
                    <Button className="btn-primary">
                        <Link
                            href={
                                feedback
                                    ? `/interview/${interviewId}/feedback`
                                    : `/interview/${interviewId}`
                            }
                        >
                            {feedback ? "Check Feedback" : "View Interview"}
                        </Link>
                    </Button>
                </div>
            </div>
        </div>
    );
};


export default InterviewCard;
